export default function Vars(){

    return {
      size: SIZE,
      maxValue: MAX_VALUE,
      icon: {
        base:     BASE_IMG_BKG,
        selected: SELECTED_IMG_BKG
      },
      styles: {
        element: ELEMENT_STYLE,
        star:    STAR_STYLE
      },
      templates : {
        css:  elementTemplate,
        html: starTemplate
      }

    };
}

export const SIZE             = "36px";
export const MAX_VALUE        = 5;
export const BASE_IMG_BKG     = 'data:image/svg+xml;base64,';
export const SELECTED_IMG_BKG = 'data:image/svg+xml;base64,';
export const ELEMENT_STYLE    = `display: flex;
                                 display: -webkit-flex;
                                 -webkit-align-items: center;
                                 -ms-align-items: center;
                                 -moz-align-items: center;
                                 align-items: center;
                                 -webkit-justify-content: center;
                                 -ms-justify-content: center;
                                 -moz-justify-content: center;
                                 justify-content: center;
                                 width: 100%;`;
export const STAR_STYLE       = `outline: 0;
                                 cursor: pointer;
                                 background-color: rgba(255,255,255,0);
                                 background-repeat: no-repeat;
                                 background-position: center center;
                                 background-size: cover;`



